.geosuggest{
  position: relative;
}
.geosuggest__suggests {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    max-height: 25em;
    padding: 0;
    margin-top: -1px;
    background: #fff;
    overflow-x: hidden;
    overflow-y: auto;
    list-style: none;
    z-index: 10;
    -webkit-transition: max-height 0.2s;
    -o-transition: max-height 0.2s;
    transition: max-height 0.2s;
}
.geosuggest__suggests--hidden{
  max-height: 0;
  overflow: hidden;
  border-width: 0;
}
.geosuggest__item--active {
